*,
::after,
::before {
  box-sizing: border-box;
}
body {
  background-image: url('../images/bodybgMob@1x.png');
  font-family: 'Montserrat';
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
  margin: 0px;
  padding: 0px;
}
ul {
  list-style: none;
  text-decoration: none;
}
